export default {
    data() {
        return {
			n:true,
			c:true,
            phone: '', //手机号
            code: '', //验证码
            iscode: '', //用于存放验证码接口里获取到的code
            codename: ' 获取验证码',
            disabled: false
        };
    },
    methods: {
		next: function() {
		    this.n = false;
		},
		back: function() {
		    this.n = true;
		},
		nextCode: function() {
		    this.c = false;
		},
		backCode: function() {
		    this.c = true;
		},
        getPhoneValue: function(e) {
            if (e.detail.value.length == 11) {
                this.disabled = false;
            }
            this.phone = e.detail.value;
        },
        getCodeValue: function(e) {
            this.code = e.detail.value;
        },
		backLogin:function(){
			uni.redirectTo({
				url: '../login/login',
			});
		},
        getCode: function() {
            var _this = this;
            //判断手机号格式
            var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
            if (_this.phone == '') {
                uni.showToast({
                    title: '手机号不能为空',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            } else if (!myreg.test(_this.phone)) {
                uni.showToast({
                    title: '请输入正确的手机号',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            } else {
                uni.request({
                    method: 'POST',
                    data: {
                        mobile: _this.phone
                    },
                    //短信接口
                    url: _this.url + 'users/forgot/sms/',
                    header: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    },

                    success(res) {
                        //根据code判断
                        var ocode = res.statusCode;
                        if (ocode == 200) {
                            _this.iscode = res.data.data;
                            var num = 61;
                            var timer = setInterval(function() {
                                num--;
                                if (num <= 0) {
                                    clearInterval(timer);
                                    _this.codename = '重新发送';
                                    _this.disabled = false;
                                } else {
                                    _this.codename = num + 's';
                                    _this.disabled = true;
                                }
                            }, 1000);
                        } else if (ocode == 400) {
                            uni.showToast({
                                title: '用户不存在',
                                icon: 'none',
                                duration: 2000
                            });
                            return false;
                        } else if (ocode == 411) {
                            uni.showToast({
                                title: '操作太频繁，请稍候重试',
                                icon: 'none',
                                duration: 2000
                            });
                            return false;
                        }
                    }
                });
            }
        },
        //获取验证码
        getCodeNumber: function(e) {
            this.getCode();
        },
        //提交表单信息
        login: function() {
            var _this = this;
            var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
            if (_this.phone == '') {
                uni.showToast({
                    title: '手机号不能为空',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            } else if (!myreg.test(_this.phone)) {
                uni.showToast({
                    title: '请输入正确的手机号',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            }
            if (_this.code == '') {
                uni.showToast({
                    title: '验证码不能为空',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            } else if (_this.code != _this.iscode) {
                uni.showToast({
                    title: '验证码错误',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            } else {
                uni.request({
                    url: _this.url + 'users/login/',
                    method: 'POST',
                    data: {
                        mobile: _this.phone,
                        code: _this.code
                    },
                    success(res) {
                        uni.setStorageSync('phone', _this.phone);
                        uni.setStorageSync('token', res.data.data);
                        _this.global_.phone = _this.phone;
                        _this.global_.token = res.data.data;
                        if (res.statusCode == 400) {
                            uni.showToast({
                                title: '验证码不正确',
                                icon: 'none'
                            });
                        }
                        if (res.statusCode == 200) {
                            uni.switchTab({
                                url: '../index/index'
                            });
                        }
                    }
                });
            }
        }
    }
};
